.login-button {
	background-color: #00be9c;
    border-radius: 3px;
    border: 2px solid white;
    color: #00be9c;
    cursor: pointer;
	fill: #00be9c;
	font-size: 15px;
	height: 50px;
    padding: 0;
    position: relative;
    stroke: #00be9c;
	white-space: nowrap;
	width: 200px
}

.login-button:hover .login-mask {
    background-color: white;
}

.login-button:hover .login-icon {
    stroke: inherit
}

.login-button:hover .login-icon > svg {
    stroke: inherit;
}

.login-button:hover .login-text {
    color: inherit;
}

.login-button:focus {
    outline: none;
}

.login-mask {
	background-color: transparent;
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	-webkit-transition: background-color 0.2s linear;
	transition: background-color 0.2s linear;
	top: 0;
}

.login-icon {
	display: inline-block;
	height: 25px;
	left: -17.5px;
	position: relative;
	top: 2px;
	width: 25px;

	> svg {
		stroke: white;
		-webkit-transition: stroke 0.2s linear;
		transition: stroke 0.2s linear;
	}
}

.login-text {
	color: white;
	display: inline-block;
	left: -12.5px;
	max-width: calc(100% - 65px);
	overflow: hidden;
	position: relative;
	text-overflow: ellipsis;
	top: -2px;
	-webkit-transition: color 0.2s linear;
	transition: color 0.2s linear;
	white-space: nowrap;
}


