.base {
    display: inline-block;
    vertical-align: top;
}

.extra-large {
    composes: base;
    width: 150px;
    height: 150px;
}

.large {
    composes: base;
    width: 100px;
    height: 100px;
}

.medium {
    composes: base;
    width: 50px;
    height: 50px;
}

.small {
    composes: base;
    width: 25px;
    height: 25px;
}

.full {
    composes: base;
    height: 100%;
    width: 100%;
}



