:root {
    /* Appearance */

    /* Named Colors */

    /* Theme Colors */

    /* State Colors */

    /* Fonts */

    /* Spacing */
}

:export {
    var-dropdown-animation-duration: 100;
}

.dropdown-selector {
	background-color: inherit;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
    color: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	height: auto;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    max-height: 100%;
    position: relative;
}

.selected-option-display {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-flex: 0;
        -ms-flex: 0 0 60px;
            flex: 0 0 60px;
    padding: 20px;
    position: relative;
}

.selected-option-display-expanded {
    background-color: #00be9c;
}

.selected-option-display-text {
    cursor: pointer;
    display: inline-block;
}

.dropdown-arrow {
    display: inline-block;
    height: 12px;
    left: 10px;
    position: relative;
    top: 1px;
    width: 12px;
}

.dropdown-x-button {
    cursor: pointer;
    height: 25px;
    position: absolute;
    right: 20px;
    top: calc(50% - 12.5px);
    width: 25px;
}

/* Add extra specificity because webpack isn't ordering the rules properly */

div.dropdown-list {
    -webkit-animation-duration: 100ms;
            animation-duration: 100ms;
    -webkit-animation-name: list-expand-out;
            animation-name: list-expand-out;
    background-color: white;
    border-top: 1px solid #ccc;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .15);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .15);
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    height: auto;
    line-height: normal;
    max-height: 360px;
    -webkit-transform-origin: top;
            transform-origin: top;
}

@-webkit-keyframes list-expand-out {
    from { -webkit-transform: scaleY(0); transform: scaleY(0); }
    to { -webkit-transform: scaleY(1); transform: scaleY(1); }
}

@keyframes list-expand-out {
    from { -webkit-transform: scaleY(0); transform: scaleY(0); }
    to { -webkit-transform: scaleY(1); transform: scaleY(1); }
}

div.dropdown-list-closing {
    -webkit-animation-duration: 100ms;
            animation-duration: 100ms;
    -webkit-animation-name: list-collapse-in;
            animation-name: list-collapse-in;
    -webkit-transform: scaleY(0);
            transform: scaleY(0)
}

@-webkit-keyframes list-collapse-in {
    from { -webkit-transform: scaleY(1); transform: scaleY(1); }
    To { -webkit-transform: scaleY(0); transform: scaleY(0); }
}

@keyframes list-collapse-in {
    from { -webkit-transform: scaleY(1); transform: scaleY(1); }
    To { -webkit-transform: scaleY(0); transform: scaleY(0); }
}

.dropdown-list-content {
    background-color: #00be9c;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.dropdown-list-item {
    border-bottom: 1px solid #f2f2f2;
    color: #3d3a3b;
    cursor: pointer;
    position: relative
}

.dropdown-list-item:last-child {
    border-bottom: none;
}

.dropdown-list-item-selected {
    color: white;
}

.dropdown-list-item-background {
    background-color: white;
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 0 /* To put background behind text */
}

.dropdown-list-item:hover .dropdown-list-item-background {
    background-color: #f7f7f7;
}

.dropdown-list-item-selected .dropdown-list-item-background {
    opacity: 0.2;
}

.dropdown-list-item-content {
    position: relative;
    z-index: 1;
}



