.webchat-notice {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-color: #00be9c;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
    position: relative;
    width: 100%;
}

.webchat-notice-message {
    color: white;
    left: 50%;
    margin-left: -150px;
    position: absolute;
    text-align: center;
    top: 60%;
    width: 300px;
}

@media screen and (max-height: 750px) {

    .webchat-notice-message {
        top: 65%;
    }
}

.custom-logo {
	position: relative;
	top: -10%;
}

.custom-logo-image {
    display: inline-block;
	max-height: 150px;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
	max-width: 150px;
}


