:root {

    /* Set z-index because display: none interacts strangely with the scrollbar */
}
:export {
    var-visible-webchat-layer: 0;
    var-hidden-webchat-layer: -1;
    var-loading-layer: 2;
}
.webchat {
	font-family: 'Lato', sans-serif;
	position: relative;
}
.loading {
    background-color: white;
    z-index: 2;
}
/* Qualify with div because webpack isn't ordering rules properly */
div.dropdown-selector {
    z-index: 1;
}
@media screen and (max-height: 150px) {
    div.dropdown-selector {
        display: none;
    }
}
.webchat-window-container {
    bottom: 0;
    left: 0;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 0;
    visibility: visible
}
.webchat-window-container.hidden {
    z-index: -1;
    visibility: hidden;
}
.webchat-window {
    z-index: 0;
}


